import React,{ useState,useEffect } from 'react'
import './App.css';

const Recipe = (props) =>{


  return(
    <div className="Recipe" onClick={props.onClick}>
        <img src={props.image} alt="nothing"/>
        <h1>{props.title}</h1>
    </div>
  );
}



const ExtendedRecipe = (props) =>{
  return(
    <div className="ExtendedRecipe">
        <img src={props.image} alt="nothing"/>
        <h1>{props.title}</h1>
    </div>

  );


}




const App = () => {
  const [Recipes,SetRecipies] = useState([]);
  const [Search,setSearch] = useState('');
  const [Query,setQuery] = useState('');
  const [SelectedRecipe,SetSelectedRecipe] = useState('none');
  const APP_ID = "10860fdf";
  const APP_KEY = "daa9cae076e037855097584d65ab1055"; 


  useEffect(()=>{
    getRecipies();
  },[Query]);

  const getRecipies = async ()=>{
    const response = await fetch(`https://api.edamam.com/search?q=${Query}&app_id=${APP_ID}&app_key=${APP_KEY}&from=0&to=20`);
    const data = await response.json();
    console.log(data);
    SetRecipies(data.hits);
  }
  const UpdateSearch = e =>{
    setSearch(e.target.value);
  }

  const SearchRecipe = e =>{
      e.preventDefault();
      setQuery(Search);
      setSearch('');
      SetSelectedRecipe('none');
  }

  const SelectRecipe = (e) =>{
    const ClickedRecipe = e.target;
    console.log(ClickedRecipe);


    SetSelectedRecipe(e.target);
  }

  const BackToMenu = () =>{
    SetSelectedRecipe('none');
  }

  return (
    <div className="App">
        <div className="Header">
          <h1 className="Logo" onClick={BackToMenu}>Food Library</h1>
          <form className="SearchForm" onSubmit={SearchRecipe}>
          <input className="SearchInput" type="text" placeholder="Search" value={Search} onChange={UpdateSearch}/>
          <button className="SearchButton" type="submit">Search</button>
          </form>
        </div>
      
    {
    SelectedRecipe=='none' ?           
    <div className="RecipesContainer">
      {
        !Recipes.length==0 ?
        Recipes.map(recipe =>(
          <Recipe title={recipe.recipe.label} image={recipe.recipe.image} onClick={SelectRecipe}/>))
        :
        <h1 className="NoResultsText">Search a recipe</h1>
      }
    </div>  
    : 
    <ExtendedRecipe title={Recipes[0].recipe.label} image={Recipes[0].recipe.image} />
    }
    
    </div>
  )}

export default App;
